<template>
<div class="page">
    <div class="wrapper">
        <ul class="video-list">
            <li class="item" v-for="video in videos">
                <div class="video">
                    <img :src="video.coverImagUrl" alt="">
                    <span class="duration">{{video.videoDuration}}</span>
                    <div class="playBtn"></div>
                </div>
                <div class="info">
                    <p class="title">{{video.title}}</p>
                    <p class="description">{{video.description}}</p>
                    <p class="releaseTime">{{video.releaseTimestamp}}</p>
                </div>
            </li>
        </ul>
    </div>
</div>
</template>
<script>
import util from "../common/util.js";
export default {
    data() {
        return {
            videos:[]
        }
    },
    mounted() {
        // 获取videos-data.json 中的数据
        this.$http
        .get('https://localhost:8080/api/videos')
        .then((response) => {
        response = response.body
        if (response.errno === 0) {
          this.videos = response.data
          for (let i = 0;i<this.videos.length; i++) {
              this.videos[i].videoDuration = util.formatTime(this.videos[i].videoDuration)
              this.videos[i].releaseTimestamp = util.formatDate(new Date(this.videos[i].releaseTimestamp))
            }
        }
      })
      .catch((response) => {
          console.log(response)
      })
       
    }
}
</script>
<style scoped>
    .page {
        width: 100%;
    }
    .wrapper ul.video-list {
        width: 100%;
    }
    ul.video-list li.item .video {
        width: 100%;
        position: relative;
    }
    li.item .video img {
        display: block;
        width: 100%;
    }
    li.item .video .duration {
        display: block;
        position: absolute;
        right: 1rem;
        bottom: 1rem;
        font-size: 1.1666666rem;
        color: #fff;
    }
    li.item .video .playBtn {
        position: absolute;
        left: 48%;
        top: 48%;
        width: 3.333333rem;
        height: 3.333333rem;
        background:  url(../assets/play.png) no-repeat;
        background-size: cover;
    }
    li.item .info {
        width: 100%;
        padding: 1rem;
        box-sizing: border-box;
    }
    li.item .info .title {
        font-size: 1.333333rem;
        text-align: center;
        margin-bottom: 1rem;
    }
    li.item .info .description {
        font-size: 1.166666rem;
        color: #939393;
        margin-bottom: 1rem;
        text-align: left;
        text-overflow: -o-ellipsis-lastline;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }
    li.item .info .releaseTime {
        color: #bfbfbf;
        font-size: 1rem;
        text-align: left;
        margin-bottom: 1rem;  
    }
</style>


