function formatDate(now) {
var month=now.getMonth()+1;
var date=now.getDate();
return month+"月"+date+"日";
}  
function formatTime(seconds) {
    return [
        parseInt(seconds / 60 % 60),
        parseInt(seconds % 60)
    ]
        .join(":")
        .replace(/\b(\d)\b/g, "0$1");
}

module.exports = {
    formatDate,
    formatTime
}


